html, body {
  --blue-color: #00acdc;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.container {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  font-style: normal;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.container .logo {
  height: 40px;
}

.navigation {
  display: flex;
}

.navigation .navigationList {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.navigationList > li:first-child a {
  margin-left: 0;
}

.nav__link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

